        if (gRoleId == "10") {
            url += `&company_id=${bypass_company_id}`;
            if(!bypass_company_id) {
                url += `&is_show_all_orders=1`;
            }
        }
        if (gIsShowOwn) {
            url += `&is_show_my_orders=1`;
        }

